.LoadingIcon {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: transform 0.2s;
}

.LoadingIcon-with-hover {
  cursor: pointer;
}

.LoadingIcon-with-hover:hover {
  transform: scale(110%);
}

.LoadingIcon-with-hover:active {
  transform: scale(150%);
}

.LoadingIcon-text {
  max-width: 300px;
  text-align: center;
  font-family: inherit;
  font-size: larger;
  font-weight: bold;
  word-break: break-word;
  white-space: pre-wrap;
}

.LoadingIcon-text-failed {
  color: red;
}

.lds-ripple {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.lds-ripple div {
  position: absolute;
  border: 4px solid #fff;
  border: 4px solid var(--accent-color);
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}

.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}

@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 0;
  }

  4.9% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 0;
  }

  5% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }

  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}